:root {
  // Core Colors.

  // Overridden by Palette Switcher.
  --brand-primary: #138aab;
  --brand-primary-dark: #107996;
  --brand-primary-accent: #159ec4;
  --brand-secondary: #e6ab49;
  --brand-secondary-dark: #9d6915;
  --brand-secondary-accent: #f1b85a;
  --mukurtu-navbar-collapse-link-hover-color: #e5ab48;
  --mukurtu-breadcrumb-gradient-color: #b7873a;
  --mukurtu-panel-pane-text-color: var(--color-white);
  --mukurtu-panel-pane-content-background-color: #eeecec;
  --mukurtu-mejs-background-color: var(--brand-light-accent);
  --mukurtu-mejs-time-rail-loaded-color: #e5e1e1;
  --mukurtu-mejs-time-rail-current-color: #d1cbcb;
  --mukurtu-dictionary-word-label-background-color: var(--mukurtu-view-row-divider-color);
  --mukurtu-collection-title-font-color: var(--color-white);
  --mukurtu-collection-pane-bg-color: var(--mukurtu-navbar-collapse-bg);

  // Persistent Across Palettes Below.
  --brand-text-color: #464646;
  --brand-light: #e5e9e9;
  --brand-light-accent: #f8f7f7;
  --link-color: var(--brand-primary-accent);
  --text-color: var(--brand-text-color);
  --light-text-color: var(--color-white);
  --teaser-text-color: #333333;

  // Drupal messages.
  --color-status-light: #dff0d5;
  --color-warning-light: #d8edfa;
  --color-error-light: #f2dedf;
  --color-status-dark: #365e38;
  --color-warning-dark: #7a97af;
  --color-error-dark: #a56560;

  // General colors.
  --color-white: #ffffff;
  --color-black: #000000;
  --color-gray: #555555;
  --color-transparent: rgba(0, 0, 0, 0);
  --focus-color: #66afe9;
  --placeholder-color: #999999;

  // Footer.
  --footer-text-color: #999999;
  --footer-link-color: #cccccc;

  // Nav.
  --navbar-default-bg: var(--color-white);
  --navbar-mukurtu-bg: var(--brand-primary);
  --main-nav-divider: rgba(70, 70, 70, 0.25); // #464646 with 25% opacity.

  // Views.
  --mukurtu-view-row-divider-color: #eeeaea;
  --mukurtu-primary-field-divider-color: #e5e1e1;

  // Mobile Menu/Collapse Navbar.
  --mukurtu-navbar-collapse-bg: var(--brand-light-accent);
  --mukurtu-navbar-collapse-link-color: var(--brand-primary-accent);

  // Mukurtu Specific.
  --mukurtu-text-box-background-color: #eeecec;
  --mukurtu-title-accent-bar-color: var(--brand-secondary);
  --mukurtu-left-right-title-color: var(--color-white);
  --mukurtu-dictionary-term-title-full-color: var(--color-white);

  // Breadcrumbs.
  --mukurtu-breadcrumb-background-color: var(--brand-secondary);
  --mukurtu-breadcrumb-link-color: var(--color-white);
  --mukurtu-breadcrumb-non-link-color: var(--text-color);

  --mukurtu-default-container-bg-color: var(--brand-primary);
  --mukurtu-default-container-bg-hover-color: var(--brand-primary-accent);
  --mukurtu-default-container-font-color: var(--color-white);

  // Mukurtu Menu Navbar links.
  --mukurtu-navbar-default-link-color: var(--color-white);
  --mukurtu-navbar-default-link-hover-color: var(--color-white);
  --mukurtu-navbar-default-link-hover-bg: var(--brand-primary-accent);

  --mukurtu-navbar-default-link-active-color: var(--color-gray);
  --mukurtu-navbar-default-link-active-bg: darken(var(--navbar-default-bg), 6.5%);
  --mukurtu-navbar-default-link-disabled-color: #cccccc;
  --mukurtu-navbar-default-link-disabled-bg: transparent;

  // Media Element Controls.
  --mukurtu-mejs-text-color: var(--brand-text-color);

  // Dictionary.
  --mukurtu-dictionary-facets-pane-background-color: var(--brand-primary);
  --mukurtu-dictionary-facets-pane-contrast-color: var(--brand-secondary);
  --mukurtu-dictionary-term-title-color: var(--color-white);

  // Collections.
  /*!These values change based on the selected color palette - see these
  at the top of the file. */

  // Community.
  --mukurtu-community-membership-status-color: var(--brand-primary);

  // Browse By.
  --mukurtu-browse-by-background-color: var(--brand-primary);
  --mukurtu-browse-by-title-color: var(--brand-secondary);
}

/*! See palettes folder for the different color palettes.
Those files are injected by Drupal so they shouldn't be bundled to style.css. */
